section.content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  place-content: center center;
}

.logo h1 {
  position: relative;
  top: 0;
  left: 0;
  font-size: calc(10px + 6.9375vw);
  line-height: calc(11px + 0.135417vh);
  padding: 24px 16px;
  border-radius: 17px;
  margin-bottom: 72px;
  color: vars.$text;
}


.title h2 {
  position: relative;
  top: 0;
  left: 0;
  font-size: calc(10px + 4.9375vw);
  line-height: calc(11px + 0.135417vh);
  padding: 24px 16px;
  border-radius: 17px;
  color: vars.$title;

}

.text h4 {
  position: relative;
  top: 0;
  left: 0;
  font-size: calc(10px + 1.9375vw);
  line-height: calc(11px + 0.135417vh);
  padding: 24px 16px;
  border-radius: 17px;
  color: vars.$descr;
}
